import React, {Component} from 'react';
import './App.css';
import 'h8k-components';
import ProductList from "./components/product-list";
import Cart from "./components/cart";

const title = "HackerShop";

class App extends Component {
    constructor() {
        super();
        const products = [...PRODUCTS].map((product, index) => {
            product.id = index + 1;
            product.image = `/images/items/${product.name.toLocaleLowerCase()}.png`;
            product.cartQuantity = 0;
            return product;
        });
        this.state = {
            cart: {
                items: []
            },
            products
        }
        this.handleAction = this.handleAction.bind(this);
        this.handleAddToCart = this.handleAddToCart.bind(this);
    }

    handleAddToCart(product){
      let cart = this.state.cart;
      let products = this.state.products;
      let index = products.findIndex((data,i)=>data.id === product.id)
      let itemForCart = {};
      // console.log(product,products,cart,index)
      products[index].cartQuantity = products[index].cartQuantity + 1 
      itemForCart.id = products[index].id;
      itemForCart.item = products[index].name;
      itemForCart.quantity = products[index].cartQuantity

      cart.items.push(itemForCart);
      this.setState({
        cart,products
      })

      // cartQuantity: 0
      // id: 1
      // image: "/images/items/cap.png"
      // name: "Cap"
      // price: 5
    }

    handleAction(action,item){
      let cart = this.state.cart;
      let products = this.state.products;
      let pIndex = products.findIndex((data,i)=>data.id === item.id)
      let cIndex = cart.items.findIndex((cartData,i)=>cartData.id === item.id)
      // console.log(cart.items,cIndex)
      
      if(action === 'add'){
        products[pIndex].cartQuantity = products[pIndex].cartQuantity + 1 
        cart.items[cIndex].quantity = products[pIndex].cartQuantity
      }else{
        products[pIndex].cartQuantity = products[pIndex].cartQuantity - 1 
        cart.items[cIndex].quantity = products[pIndex].cartQuantity
        if(!cart.items[cIndex].quantity){
          cart.items.splice(cIndex,1);
        }
      }
      
      this.setState({
        cart,products
      })
    }


    render() {
        return (
            <div>
                <h8k-navbar header={title}></h8k-navbar>
                <div className="layout-row shop-component">
                    <ProductList handleAction={(action,item)=>this.handleAction(action,item)} handleAddToCart={(product)=>this.handleAddToCart(product)}  products={this.state.products}/>
                    <Cart cart={this.state.cart}/>
                </div>
            </div>
        );
    }
}

export const PRODUCTS = [
    {
        name: "Cap",
        price: 5
    },
    {
        name: "HandBag",
        price: 30
    },
    {
        name: "Shirt",
        price: 35
    },
    {
        name: "Shoe",
        price: 50
    },
    {
        name: "Pant",
        price: 35
    },
    {
        name: "Slipper",
        price: 25
    }
];
export default App;
